import { BehaviorSubject } from 'rxjs';
import config from 'react-global-configuration';
// import configuration from '../config';
import axios from 'axios';
import { handleResponse } from '../_helpers';

// config.set(configuration);
const currentUserSubject = new BehaviorSubject(JSON.parse(localStorage.getItem('currentUser')));

export const authenticationService = {
    login,
    register,
    logout,
    currentUser: currentUserSubject.asObservable(),
    get currentUserValue() { return currentUserSubject.value }
};


function register(username, email, password, password_confirmation) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body:  { username, password, email, password_confirmation }
    };
    return axios.post(`${config.get('apiUrl')}/auth/register`, requestOptions.body)
       
        .then(response => {


            return response;
        });

}
function login(username, password) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, password })
    };

    return fetch(`${config.get('apiUrl')}/auth/login`, requestOptions)
        .then(handleResponse)
        .then(user => {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            localStorage.setItem('currentUser', JSON.stringify(user));
            currentUserSubject.next(user);

            return user;
        });
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('currentUser');
    currentUserSubject.next(null);
}
