import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { authenticationService } from '../../../_services';

class Login extends Component {
  constructor(props) {
    super(props);

    // redirect to home if already logged in
    if (authenticationService.currentUserValue) {
      this.props.history.push('/');
    }
  }
  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Formik
                      initialValues={{
                        username: '',
                        password: ''
                      }}
                      validationSchema={Yup.object().shape({
                        username: Yup.string().required('Username is required').min(6,"Too short"),
                        password: Yup.string().required('Password is required')
                      })}
                      onSubmit={({ username, password }, { setStatus, setSubmitting }) => {
                        setStatus();
                        authenticationService.login(username, password)
                          .then(
                            user => {
                              const { from } = this.props.location.state || { from: { pathname: "/" } };
                              this.props.history.push(from);
                            },
                            error => {
                              setSubmitting(false);
                              console.log(error);

                              // setStatus(error);
                            }
                          );
                      }} >
                      {({ errors, status, touched, isSubmitting }) => (
                        <Form>
                          <h1>Login</h1>
                          <p className="text-muted">Sign In to your account</p>
                          <InputGroup className="mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="icon-user"></i>
                              </InputGroupText>
                            </InputGroupAddon>
                            <Field type="text"
                              name="username"
                              className={'form-control' + (errors.username && touched.username ? ' is-invalid' : '')}
                              placeholder="Username" autoComplete="username" />
                            <ErrorMessage name="username" component="div" className="invalid-feedback" />

                          </InputGroup>
                          <InputGroup className="mb-4">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="icon-lock"></i>
                              </InputGroupText>
                            </InputGroupAddon>
                            <Field type="password" name="password" placeholder="Password"
                              className={'form-control' + (errors.password && touched.password ? ' is-invalid' : '')}
                              autoComplete="current-password" />
                            <ErrorMessage name="password" component="div" className="invalid-feedback" />

                          </InputGroup>
                          <Row>
                            <Col xs="6">
                              <Button color="primary" disabled={isSubmitting} className="px-4">Login</Button>
                            </Col>
                            <Col xs="6" className="text-right">
                              <p color="primary" type="submit" className="px-0"><a href="mailto:scitvers@gmail.com">trouble logging in, send us a mail?</a></p>
                              {isSubmitting &&
                                <img alt="loading" src="data:image/gif;base64," />
                              }

                            </Col>
                          </Row>
                          {status && <div className={'alert alert-danger'}>{status}</div>}
                        </Form>
                      )}

                    </Formik>
                  </CardBody>
                </Card>
                <Card className="text-white bg-purple py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p> no account yet? click the button below to register</p>
                      <Link to="/register">
                        <Button color="primary" type="button" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>

      </div>
    );
  }
}

export default Login;
