import React from 'react';
import { Link } from 'react-router-dom';
import {
    Button,
    Navbar,
} from 'reactstrap';
import { LogoThemed } from
    '../../../../containers/DefaultLayout/LogoThemed/LogoThemed';
const navButtons = {
    marginRight: '5px',
};
export const LandingLayout = () => (
    <React.Fragment>
        <Navbar dark shadow={'true'} expand="lg" className="py-3 bg-white">
            <Link to="/" className="navbar-brand mr-0 mr-sm-3">
                <LogoThemed className="mb-1"  color="purple" />
            </Link>

            <div className='right' >
                <Link to="/login">
                    <Button style={navButtons} color='purple' className="px-4 my-sm-0">
                        Login
                    </Button>
                </Link>
                <Link to="/register">
                    <Button style={navButtons} color='purple' className="px-4 my-sm-0">
                        Register
                    </Button>
                </Link>
            </div>
        </Navbar>
    </React.Fragment>
);
