import React, { Component } from 'react';
import { Button, Card, CardBody, Col, Container, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { authenticationService } from '../../../_services';
class Register extends Component {
 

  render() {

    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardBody className="p-4">
                  <Formik
                    initialValues={{
                      username: '',
                      password: '',
                      email: '',
                      password_confirmation: ''
                    }}
                    validationSchema={Yup.object().shape({
                      username: Yup.string().min(5, 'Too Short!').max(50, 'Too Long!').required('Required').required('Username is required'),

                      password: Yup.string().required('Password is required'),

                      password_confirmation: Yup.string()
                        .oneOf([Yup.ref('password'), null], 'Passwords must match'),
                      email: Yup.string().email('Please enter a valid email address')
                    })}
                    onSubmit={({ username, password, email, password_confirmation }, { setStatus, setSubmitting }) => {
                      setStatus();
                      authenticationService.register(username, email,password,  password_confirmation)
                        .then(
                          user => {
                            const { from } = this.props.location.state || { from: { pathname: "/login" } };
                            this.props.history.push(from);
                          },
                          error => {
                            setSubmitting(false);
                            console.log(error);

                            // setStatus(error);
                          }
                        );
                    }} >
                    {({ errors, status, touched, isSubmitting }) => (
                      <Form>
                        <h1>Register</h1>
                        <p className="text-muted">Create your account</p>
                        <InputGroup className="mb-3">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="icon-user"></i>
                            </InputGroupText>
                          </InputGroupAddon>
                          <Field type="text" name="username" 
                           className={'form-control' + (errors.username && touched.username ? ' is-invalid' : '')}
                          placeholder="Username" autoComplete="username" />
                          <ErrorMessage name="username" component="div" className="invalid-feedback" />

                        </InputGroup>
                        <InputGroup className="mb-3">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>@</InputGroupText>
                          </InputGroupAddon>
                          <Field type="text"
                           className={'form-control' + (errors.email && touched.email ? ' is-invalid' : '')}
                          name="email" placeholder="Email" autoComplete="email" />
                          <ErrorMessage name="email" component="div" className="invalid-feedback" />

                        </InputGroup>
                        <InputGroup className="mb-3">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="icon-lock"></i>
                            </InputGroupText>
                          </InputGroupAddon>
                          <Field type="password" name="password"
                           className={'form-control' + (errors.password && touched.password ? ' is-invalid' : '')}
                          placeholder="Password" autoComplete="new-password" />
                          <ErrorMessage name="password" component="div" className="invalid-feedback" />

                        </InputGroup>
                        <InputGroup className="mb-4">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="icon-lock"></i>
                            </InputGroupText>
                          </InputGroupAddon>
                          <Field type="password"
                           className={'form-control' + (errors.password_confirmation && touched.password_confirmation ? ' is-invalid' : '')}
                          name="password_confirmation" placeholder="Repeat password" autoComplete="new-password" />
                          <ErrorMessage name="password_confirmation" component="div" className="invalid-feedback" />

                        </InputGroup>
                        <Button type="submit" color="success" block>
                          Create Account
                          {isSubmitting &&
                                <img alt="loading" src="data:image/gif;base64," />
                              }
                               {status && <div className={'alert alert-danger'}>{status}</div>}
                        </Button>
                      
                      </Form>
                    )}
                  </Formik>
                </CardBody>
               
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Register;
