const Dashboard = React.lazy(() => import('./views/Dashboard'));
const UserQuizzes = React.lazy(() => import('./views/Quizzes/UserQuizzes'));
const UserQuiz = React.lazy(() => import('./views/Quizzes/UserQuiz'));
const Profile = React.lazy(()=> import('./views/Profile/Profile'));
const Quiz = React.lazy(()=>import('./views/Dashboard/quiz/Quiz'));
const QuestionContainer = React.lazy(()=> import('./views/Dashboard/quiz/questions/QuestionContainer'));
const Results = React.lazy(()=>import('./views/Results/UserResults'));
const Result = React.lazy(()=>import('./views/Results/UserResult'));
// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home', component: Dashboard  },
  { path: '/dashboard', name: 'Home', component: Dashboard },
  { path: '/quiz/:id',name:'Quiz', component:Quiz},
  { path:'/questions/:username/:id', component:QuestionContainer},
  { path:'/profile', name:'Profile', component : Profile},
  { path:'/responses/:quizLink', name:'Responses', component: Results},
  { path:'/response/:quizLink/:name', name:'Response Details', component: Result},
  { path: '/user-quiz', exact: true,  name: 'Your Quizzes', component: UserQuizzes },
  { path: '/user-quiz/:id', exact: true, name: 'Quiz Details', component: UserQuiz },

];

export default routes;
