import React, { Component } from 'react';
import {  Route, Switch , BrowserRouter} from 'react-router-dom';
// import { renderRoutes } from 'react-router-config';
import './App.scss';
import { Landing } from './views/Pages';
import PrivateRoute from './_components/PrivateRoute'
import DefaultLayout from './containers/DefaultLayout/DefaultLayout';
const loading = () => <div className="animated fadeIn pt-3 text-center">Loading...</div>;

// Containers
// const DefaultLayout = React.lazy(() => import('./containers/DefaultLayout'));

// Pages
const Login = React.lazy(() => import('./views/Pages/Login'));
const Register = React.lazy(() => import('./views/Pages/Register'));
const Page404 = React.lazy(() => import('./views/Pages/Page404'));
const Page500 = React.lazy(() => import('./views/Pages/Page500'));
const ComingSoon = React.lazy(() => import('./views/Pages/ComingSoon'));
const Response = React.lazy(()=>import('./views/Pages/quiz/startPage/GeneralSection'));
const Questions = React.lazy(()=>import('./views/Pages/quiz/questions/QuestionsContainer'));
const Results = React.lazy(()=> import('./views/Pages/Results/Results'));
const PrivacyPolicy = React.lazy(()=> import('./views/Pages/PrivacyPolicy/PricvacyPolicy'));
class App extends Component {

  render() {
    return (
      <BrowserRouter>
          <React.Suspense fallback={loading()}>
            <Switch>
              <Route exact path="/privacy" name="Privacy Policy" render ={props => <PrivacyPolicy {...props}/>} />
              <Route exact path="/login" name="Login Page" render={props => <Login {...props}/>} />
              <Route exact path="/register" name="Register Page" render={props => <Register {...props}/>} />
              <Route exact path="/404" name="Page 404" render={props => <Page404 {...props}/>} />
              <Route exact path="/ComingSoon" name="Coming Soon" render={props => <ComingSoon {...props}/>} />
              <Route exact path="/500" name="Page 500" render={props => <Page500 {...props}/>} />
              <Route exact path="/landing" name = "Landing page" render={props => <Landing {...props}/> }/>
              <Route exact path="/response/:userId" name ="Response" render={props=> <Response {...props} />}  />
              <Route exact path="/answer/:userId/:quizLink" name="Answer Question" render={props=> <Questions {...props} />}  />
              <Route exact path="/results/:name/:link" name="Results" render={props=> <Results {...props} />} />
              <PrivateRoute path="/" name="Home"  component ={DefaultLayout} />
            </Switch>
          </React.Suspense>
      </BrowserRouter>
    );
  }
}

export default App;
